import React from "react";
// 导入常量
import {Themes} from "../../assest/Theme"
import { Algorithm_Note } from "../../assest/algorithm";
// markdown样式
import {marked} from 'marked'
import hljs from 'highlight.js' // 导入markdown样式包
import "highlight.js/styles/atom-one-light.css"
// redux
import {useSelector} from 'react-redux'

interface NoteProps{
    Theme_begin: any,
    WidthPage: number,
    HeightPage: number
}

interface NoteState{
}

function Note(props: NoteProps, state: NoteState){
    const {Theme_begin, WidthPage, HeightPage} = props
    let Theme = {
        ...Theme_begin,
        width: Theme_begin.width * WidthPage - 4,
        height: Theme_begin.height * HeightPage - Themes.TitleTheme.height,
        left: Theme_begin.left * WidthPage
    }
    // redux
    const {AlgorithmNow, ModelNow} = useSelector(state => (state as any).Reducer_module)

    function Get_Note(algorithm_now: string, model_now: string){
        return marked((Algorithm_Note as any)[algorithm_now][model_now])
    }

    React.useEffect(() => {
        let markdown_element: any = document.getElementById("Note-Container")
        markdown_element.innerHTML = Get_Note(AlgorithmNow, ModelNow)
        hljs.highlightAll() // 高亮代码块
    }, [ModelNow])

    return (
        <div className="Note" style={{...Theme}}>
            <div
                id="Note-Container" 
                style={{float: 'left', width: Theme.width, height: Theme.height, overflow: 'auto'}}></div>
        </div>
    )
}

export default Note  